html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  background-color: #fedd6a;
  overflow: hidden;
}

picture {
  width: 100%;
  height: auto;
}

#container {
  width: 1920px;
  height: 1200px;
  opacity: 0;
  transition: opacity 1s;
}

@keyframes bounce-character {
  0% {
    transform: translateY(50px);
  }

  100% {
    transform: translateY(0px);
  }
}

#character {
  position: relative;
  width: 100%;
  height: 100%;
  /*height: auto;*/
  /*max-width: 1920px;*/
  /*max-height: 1200px;*/
  /*width: 1110px;*/
  /*height: 1136px;*/
  /*max-width: 1110px;*/
  /*max-height: 1136px;*/
  /*width: 100%;*/
  /*height: auto;*/
  animation: bounce-character 150ms infinite;
  animation-direction: alternate;
}

[id^="teto-"] {
  position: absolute;
}

@keyframes bounce-body {
  0% {
    transform: translateY(-20px);
  }

  100% {
    transform: translateY(0px);
  }
}

#teto-body {
  position: absolute;
  top: 0;
  left: 0;
  animation: bounce-body 150ms infinite;
  animation-direction: alternate;
}

@keyframes bounce-twintail-right {
  0% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(-20deg);
  }
}

#twintail-right {
  position: absolute;
  top: 215px;
  transform-origin: 300px 90px;
  animation: bounce-twintail-right 150ms infinite;
  animation-direction: alternate;
}

@keyframes bounce-twintail-left {
  0% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(20deg);
  }
}

#twintail-left {
  position: absolute;
  top: 130px;
  left: 715px;
  transform-origin: 0px 100px;
  animation: bounce-twintail-left 150ms infinite;
  animation-direction: alternate;
}

@keyframes left-hand-shake {
  0% {
    transform: translateY(10px) rotate(-3deg);
  }

  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

#gyu-left-hand {
  position: absolute;
  top: -65px;
  left: -430px;
  /*top: -7%;*/
  /*left: -39%;*/
  transform-origin: 1400px 1450px;
  /*transform-origin: 1300px 1300px;*/
  /*transform-origin: 73% 75%;*/
  animation: left-hand-shake 150ms infinite;
  animation-direction: alternate;
}
